
body {
  background-image: url("./images/workspace.jpg");
  text-align: center;
  background-size: cover;
  background-color: gray;
  margin: auto;
  color: black;
}
router-link:link {
  color: black;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: black;
  margin-top: 60px;
}
.user {
  color: white;
  font-weight: bold;
  font-size: 20px;
  margin-right: 75px;
  margin-top: 15px;
}
li {
  list-style: none;
}
.getout {
  margin-top: 11px;
  margin-left: 10px;
  margin-right: 10px;

  opacity: .8;
  border-radius: 10px;
  margin: 10px 10px 10px 0;
}
.dropdown:hover .dropdown-menu {
  display: block;
}
.firstRow {
  height: 4%;
}
#app {
  margin: 0;
  padding: 0;
}



